import { Button, Form } from "react-bootstrap";
import styles from "./SearchBar.module.scss";
import { Autocomplete, TextField } from "@mui/material";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { setData, resetData } from "../../features/weather/WeatherSlice";
export const SearchBar = () => {
  const [cities, setCities] = useState([]);
  const [unity, setUnity] = useState("metric");
  const dispatch = useDispatch();

  const handleInputChange = (e) => {
    const { value } = e.currentTarget;
    fetch(
      `https://api.geoapify.com/v1/geocode/autocomplete?text=${value}&type=city&format=json&apiKey=0ebb9585c9e742ba95a9cf14d7dff24f`
    )
      .then((response) => response.json())
      .then((json) =>
        setCities(
          json.results?.map((data) => {
            const { lat, lon, city, country, formatted } = data;
            return { lat, lon, city, country, formatted };
          })
        )
      );
    console.log(value);
  };
  const handleAutocompleteSelecte = (e, value) => {
    if (value !== null) {
      const { lon, lat } = value;
      fetch(
        `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&units=${unity}&lon=${lon}&appid=fd9bb5b2f646b434a78a9152c0cf9a7e`
      )
        .then((response) => response.json())
        .then((json) => {
          const { clouds, main, name, sys, weather, wind } = json;
          dispatch(setData({ clouds, main, name, sys, weather, wind }));
        });
    } else {
      dispatch(resetData());
    }
  };
  return (
    <>
      <Form>
        <Form.Group className={styles.searchContainer}>
          <Autocomplete
            className={styles.searchInput}
            clearOnBlur={false}
            getOptionLabel={(option) => option.formatted}
            onChange={handleAutocompleteSelecte}
            renderInput={(params) => (
              <TextField
                onChange={handleInputChange}
                {...params}
                label={"Enter your city ..."}
              />
            )}
            options={cities}
          />

          <Button size={"sm"} variant="primary">
            Search
          </Button>
        </Form.Group>
      </Form>
    </>
  );
};
