import styles from "./Weather.module.scss";
import { Card } from "react-bootstrap";
import PositionSvg from "../Svgs/PositionSvg";
import DefaultWeather from "../Svgs/DefaultWeather";
import Thermometer from "../Svgs/Thermometer";
import Time from "../Svgs/Time";
import Wind from "../Svgs/Wind";
import SpeedoMeter from "../Svgs/SpeedoMeter";
import Humidity from "../Svgs/Humidity";
import { useSelector } from "react-redux";
import Moment from "react-moment";
import clearSky from "../Pngs/clear-sky.png";
import fewclouds from "../Pngs/few-clouds.png";
import scatteredclouds from "../Pngs/scattered-clouds.png";
import brokenclouds from "../Pngs/broken-clouds.png";
import showerrain from "../Pngs/shower-rain.png";
import rain from "../Pngs/rain.png";
import thunderstorm from "../Pngs/thunderstorm.png";
import snow from "../Pngs/snow.png";
import mist from "../Pngs/mist.png";

export const Weather = () => {
  const weather = useSelector(({ weather }) => weather);
  const displayIcon = () => {
    const defaultWidth = "200px";
    const defaultHeight = "200px";
    const number = weather.weather.icon.substring(0, 2);
    switch (number) {
      case "01":
        return (
          <img
            src={clearSky}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "02":
        return (
          <img
            src={fewclouds}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "03":
        return (
          <img
            src={scatteredclouds}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "04":
        return (
          <img
            src={brokenclouds}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "09":
        return (
          <img
            src={showerrain}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "10":
        return (
          <img
            src={rain}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "11":
        return (
          <img
            src={thunderstorm}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "13":
        return (
          <img
            src={snow}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      case "50":
        return (
          <img
            src={mist}
            alt="aaaa"
            height={defaultHeight}
            width={defaultWidth}
          />
        );
      default:
        return (
          <img
            src={`https://openweathermap.org/img/wn/${weather.weather.icon}@2x.png`}
            alt=""
          />
        );
    }
  };
  console.log(weather);
  return (
    <>
      <Card className={styles.container}>
        {weather.isLoaded ? (
          <Card.Body>
            <Card.Title>
              {weather.name} , {weather.sys.country}{" "}
              <PositionSvg color={"rgba(255,255,255,0.7)"} />
              <div className={styles.date}>
                <div>
                  <Moment format={"llll"} />
                </div>
                <div>
                  <Time width={"15px"} height={"15px"} />
                </div>
              </div>
            </Card.Title>

            <Card.Text as={"div"} className={styles.weather_infos}>
              <div>{displayIcon()}</div>
              <div className={styles.temperature}>
                <div>{weather.main.feels_like}</div>
                <div>
                  <Thermometer />
                </div>
              </div>
              <div>
                Good Morning {weather.name}
                <div className={styles.separator}></div>
              </div>
              <div className={styles.infos}>
                <div className={styles.border_right}>
                  <div>
                    <DefaultWeather color={"#fff"} />
                  </div>
                  <div>Sunrise</div>
                  <div>
                    <Moment unix={true} format={"hh:mm"}>
                      {weather.sys.sunrise}
                    </Moment>
                  </div>
                </div>
                <div className={styles.border_right}>
                  <div>
                    <Wind />
                  </div>
                  <div>Wind</div>
                  <div>{weather.wind.speed} m/s</div>
                </div>
                <div className={styles.border_right}>
                  <div>
                    <SpeedoMeter color={"#fff"} />
                  </div>
                  <div>Pressure</div>
                  <div>{weather.main.pressure} Pa</div>
                </div>
                <div className={styles.border_right}>
                  <div>
                    <Humidity />
                  </div>
                  <div>Humidity</div>
                  <div>{weather.main.humidity}%</div>
                </div>
                <div>
                  <div>
                    <Thermometer
                      color={"#fff"}
                      width={"25px"}
                      height={"25px"}
                    />
                  </div>
                  <div>Temp</div>
                  <div>{weather.main.temp_max} C</div>
                </div>
              </div>
            </Card.Text>
          </Card.Body>
        ) : (
          <Card.Body>
            <Card.Title>Please choose a city.</Card.Title>
          </Card.Body>
        )}
      </Card>
    </>
  );
};
